<template>
  <div>
    我是userEdit
    <div style="height:1000px;background-color:#ccc;"></div>
    <button id="target">按钮</button>
  </div>
</template>

<script>
export default {
  data(){
    return{
      confirm:false,
    }
  },
  // 3.写在组件内部的路由
  beforeRouteEnter(to,from,next){
    // this获取不到数据，因为还没有进入该路由呢=>next的回调函数中获取vm实例

    console.log("edit组件内的路由守卫-beforeRouteEnter");
    next((vm)=>{
      console.log("vm:",vm);
      
    })
  },
  beforeRouteLeave(to,from,next){
       console.log("edit组件内的路由守卫-beforeRouteLeave");
       if(this.confirm){
         next()
       }else{
         if(confirm("确定要离开吗")){
           next()
         }else{
           next(false)
         }
       }
  }
};
</script>

