<template>
  <div class="user">
    <button @click="goHome">跳转到主页</button>
    <h1>This is an user page</h1>
    <!-- <p>{{id}}</p> -->
    <!-- this.$router获取vue实例下的router实例-->


     <!-- <transition name="fade" mode="out-in"> -->
    <router-view></router-view>
     <!-- </transition> -->
  </div>
</template>

<script>
export default {
    name:'user',
  data() {
    return {
      id: this.$route.params.id //route:当前路由信息；router实例
    };
  },
  created() {
    console.log(this.$route)
  },
  watch:{
      $route(to,from){
          console.log(to,from);
          
          this.id =  to.params.id;
      }
  },
  methods: {
    goHome() {
      // this.$router.push('/')
      //or
      this.$router.push({ path: "/" });
    }
  }
};
</script>

